.example-list-item {
  width: 100%;
  max-width: 350px;
  margin-bottom: 30px;
  border-radius: 2px;
}
.example-list-item .example-link {
    position: relative;
    display: block;
}
.example-list-item .example-link .chart-area {
      width: 100%;
      height: 100%;
      border-radius: 5px;
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
      margin-top: 10px;
}
.example-list-item .example-link .example-title {
      color: #293c55;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      padding: 10px 10px 2px 10px;
      margin: 0;
      font-weight: normal;
      font-size: 14px;
      text-align: center;
}
.example-list-item .example-link .example-subtitle {
      font-size: 12px;
      text-align: center;
      color: #aaa;
      font-weight: normal;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      margin: 3px 0 0 0;
}
.example-list-item .example-info {
    padding: 5px 0;
    font-weight: bold;
}
.example-list-item .example-info .chart-icon {
      float: right;
}
.example-list-item .example-info .chart-icon .chart-delete {
        display: none;
        transition: 1s;
}
.example-list-item:hover .example-info .chart-icon .chart-delete {
    display: block;
    text-decoration: none;
}
