#example-explore {
  background: #f3f4fa;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow-y: auto;
}
#example-explore ::-webkit-scrollbar {
    height: 4px;
    width: 4px;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    border-radius: 2px;
    background: #fff;
}
#example-explore ::-webkit-scrollbar-button {
    display: none;
}
#example-explore ::-webkit-scrollbar-thumb {
    width: 4px;
    min-height: 15px;
    background: rgba(50, 50, 50, 0.2) !important;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    border-radius: 2px;
}
#example-explore ::-webkit-scrollbar-thumb:hover {
    background: rgba(0, 0, 0, 0.5) !important;
}
#explore-container {
  margin-left: 220px;
  padding: 10px 10px;
}
.example-list-panel {
  margin: 30px 15px 30px 15px;
}
.example-list-panel h3 {
    margin-bottom: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid #E1E5F2;
    font-weight: normal;
    color: #464646;
    font-size: 20px;
}
.example-list-panel .chart-type-head span {
    font-size: 16px;
    padding-left: 5px;
    color: #949CB1;
    font-weight: 200;
}
#left-container {
  position: sticky;
  left: 0;
  top: 0;
  float: left;
  height: calc(100%);
  width: 200px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
}
#toolbar {
  position: fixed;
  right: 30px;
  top: 20px;
  height: 30px;
  background-color: #fff;
  border-radius: 15px;
  padding: 4px 15px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
#toolbar .el-switch__label * {
    font-size: 12px;
    text-transform: uppercase;
}
#toolbar .el-switch__label.is-active {
    color: #181432;
}
#left-chart-nav {
  background-color: #fff;
  overflow-y: hidden;
  color: #111;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}
#left-chart-nav:hover {
    overflow-y: auto;
}
#left-chart-nav:hover a {
      text-decoration: none;
}
#left-chart-nav li {
    cursor: pointer;
}
#left-chart-nav li a {
      height: 45px;
      padding: 10px 0 10px 20px;
      display: block;
      text-decoration: none;
      color: #6E7079;
}
#left-chart-nav li a .chart-name {
        display: inline-block;
        position: relative;
        vertical-align: middle;
        margin-left: 10px;
}
#left-chart-nav li a .chart-icon {
        content: '';
        width: 20px;
        display: inline-block;
        border-radius: 50%;
        vertical-align: middle;
}
#left-chart-nav li a .chart-icon svg {
          width: 100% !important;
          height: auto !important;
}
#left-chart-nav li a.active {
        background-color: #5470C6;
        color: #fff;
}
#left-chart-nav li a.active .chart-icon * {
          fill: #fff;
}
#left-chart-nav li a.active:hover {
        color: #fff;
}
#left-chart-nav li:hover {
      border-right: 4px solid #5470C6;
}
#left-chart-nav li:hover a {
        color: #464646;
}
@media (max-width: 768px) {
#left-container {
    display: none;
}
#explore-container {
    margin-left: 0;
}
}
