#chart-panel {
  position: absolute;
  top: 42px;
  right: 15px;
  bottom: 15px;
  left: 15px;
  box-sizing: border-box;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 20px;
  border-radius: 5px;
  background: #fff;
  overflow: hidden;
  padding: 10px;
}
#chart-panel .ec-debug-dirty-rect-container {
    left: 10px !important;
    top: 10px !important;
    right: 10px !important;
    bottom: 10px !important;
}
#chart-panel .ec-debug-dirty-rect-container .ec-debug-dirty-rect {
      background-color: rgba(255, 0, 0, 0.2) !important;
      border: 1px solid red !important;
}
.render-config-container .el-radio-group label {
  margin-bottom: 0;
}
#tool-panel {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  padding-top: 5px;
  padding-left: 15px;
}
#tool-panel .render-config-trigger {
    margin-left: 10px;
    cursor: pointer;
    font-weight: 500;
}
#tool-panel label {
    margin-bottom: 0;
}
#tool-panel .left-panel {
    float: left;
}
#tool-panel .left-panel > * {
    vertical-align: middle;
    display: inline-block;
}
#tool-panel .tool-label {
    font-weight: bold;
    text-transform: uppercase;
    margin-left: 20px;
}
#tool-panel .screenshot, #tool-panel .download, #tool-panel .edit {
    float: right;
    margin-right: 15px;
    cursor: pointer;
}
#tool-panel .screenshot {
    font-size: 22px;
    margin-right: 10px;
}
.full #chart-panel {
  top: 40px;
  right: 5px;
  bottom: 5px;
  left: 5px;
  box-shadow: rgba(10, 9, 9, 0.1) 0px 0px 5px;
}
.full #tool-panel {
  padding-left: 5px;
}
.full #tool-panel .download, .full #tool-panel .edit {
    margin-right: 5px;
}
.dg.main * {
  box-sizing: content-box;
}
.dg.main input {
  line-height: normal;
}
.dg.main.a {
  overflow-x: visible;
}
.dg.main .c select {
  color: #000;
}
